.conteiner-hourly {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    justify-content: center;


    
}
.hourly {
    border-radius: 5px;
    height: fit-content;
    flex-grow: 1;
    background-color: rgb(138, 166, 190);
    margin: 15px 0 0 2%;
    box-shadow: 5px 10px 10px black;
    width: calc(100% * (1/4) - 15px - 1px);

  }
  
  .hourly p:nth-child(1) {
    text-align: center;
    font-weight: bold;
    font-size: 13px;
    margin-bottom: 10px;
    color: rgb(65, 57, 57);
  }
  .hourly p:nth-child(3) {
    text-align: center;
    font-weight: bold;
    margin: 0;
    font-size: 13px;
    font-family: "Times New Roman", Times, serif;
  }
  .hourly p:nth-child(4) {
    text-align: center;
    font-weight: bold;
    font-size: 10px;
    font-family: "Times New Roman", Times, serif;
    color: rgb(65, 57, 57);
  }
  
  .image {
    display: flex;
    justify-content: center;
  }
  
  @media only screen and (max-width: 600px) {
    .hourly {
      border-radius: 5px;
      height: fit-content;
      flex-grow: 1;
      background-color: rgb(138, 166, 190);
      margin: 10px 0 0 3%;
      box-shadow: 5px 10px 10px black;
      width: calc(100% * (1/2) - 50px - 2px);
      max-width: 120px;
      min-width: 100px;
  
    }
  }